.dynamicImgContainer.img-small {
  min-height: 50px;
}
.dynamicImgContainer.addBorderOnDynamicImg {
  box-sizing: border-box;
  border: 1px solid rgba(250, 250, 250, 0.5);
}
.img-medium {
  min-height: 350px;
  box-sizing: border-box;
  background: #EEEEEE;
}
.prod-small {
  min-height: 150px;
  box-sizing: border-box;
  background: #EEEEEE;
  border: 1px solid rgba(250, 250, 250, 0.5);
}
.dynamicImgContainer {
  position: relative;
  max-height: 100%;
  height: 100%;
  img {
    // filter: blur(0px);
    opacity: 1;
    transition: opacity 0.3s cubic-bezier(0.215, 0.61, 0.355, 1) 0s,
    transform 0.3s ease-out 0s;
  }
  &.lazyloading {
    background: #eef0f5;
  }
  &.lazyloaded {
    img {
      opacity: 0;
      // filter: blur(2px);
    }
  }
}